/**
 * 加载状态管理 Composable
 * 统一管理加载、错误和空状态
 */

import { ref, computed } from 'vue'

export interface LoadingState {
  loading: boolean
  error: Error | null
  isEmpty: boolean
}

export interface UseLoadingStateOptions {
  initialLoading?: boolean
  onError?: (error: Error) => void
}

export function useLoadingState(options: UseLoadingStateOptions = {}) {
  const { initialLoading = false, onError } = options

  const loading = ref(initialLoading)
  const error = ref<Error | null>(null)
  const isEmpty = ref(false)

  const isLoading = computed(() => loading.value)
  const hasError = computed(() => error.value !== null)
  const isEmptyState = computed(() => isEmpty.value && !loading.value && !error.value)

  /**
   * 开始加载
   */
  const startLoading = () => {
    loading.value = true
    error.value = null
    isEmpty.value = false
  }

  /**
   * 停止加载
   */
  const stopLoading = () => {
    loading.value = false
  }

  /**
   * 设置错误
   */
  const setError = (err: Error | string) => {
    loading.value = false
    error.value = err instanceof Error ? err : new Error(err)
    
    if (onError && error.value) {
      onError(error.value)
    }
  }

  /**
   * 清除错误
   */
  const clearError = () => {
    error.value = null
  }

  /**
   * 设置空状态
   */
  const setEmpty = (empty: boolean) => {
    isEmpty.value = empty
  }

  /**
   * 重置所有状态
   */
  const reset = () => {
    loading.value = false
    error.value = null
    isEmpty.value = false
  }

  /**
   * 执行异步操作并自动管理状态
   */
  const execute = async <T>(
    fn: () => Promise<T>,
    options?: {
      checkEmpty?: (result: T) => boolean
      onSuccess?: (result: T) => void
      onError?: (error: Error) => void
    }
  ): Promise<T | null> => {
    try {
      startLoading()
      const result = await fn()
      
      // 检查是否为空
      if (options?.checkEmpty) {
        isEmpty.value = options.checkEmpty(result)
      }
      
      // 成功回调
      if (options?.onSuccess) {
        options.onSuccess(result)
      }
      
      return result
    } catch (err) {
      const errorObj = err instanceof Error ? err : new Error(String(err))
      setError(errorObj)
      
      // 错误回调
      if (options?.onError) {
        options.onError(errorObj)
      }
      
      return null
    } finally {
      stopLoading()
    }
  }

  return {
    // 状态
    loading,
    error,
    isEmpty,
    
    // 计算属性
    isLoading,
    hasError,
    isEmptyState,
    
    // 方法
    startLoading,
    stopLoading,
    setError,
    clearError,
    setEmpty,
    reset,
    execute
  }
}
