<template>
  <div class="error-container">
    <div class="error-icon">
      <slot name="icon">
        <svg width="64" height="64" viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M32 8C18.745 8 8 18.745 8 32s10.745 24 24 24 24-10.745 24-24S45.255 8 32 8zm0 44c-11.028 0-20-8.972-20-20s8.972-20 20-20 20 8.972 20 20-8.972 20-20 20z" fill="currentColor" opacity="0.3"/>
          <path d="M38.828 22.172a2 2 0 00-2.828 0L32 26.172l-4-4a2 2 0 10-2.828 2.828L29.172 29l-4 4a2 2 0 102.828 2.828L32 31.828l4 4a2 2 0 102.828-2.828L34.828 29l4-4a2 2 0 000-2.828z" fill="currentColor"/>
        </svg>
      </slot>
    </div>
    <h3 class="error-title">{{ title }}</h3>
    <p class="error-message">{{ errorMessage }}</p>
    <div v-if="$slots.action || showRetry" class="error-action">
      <slot name="action">
        <button v-if="showRetry" @click="handleRetry" class="error-retry-btn">
          重试
        </button>
      </slot>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'

const props = withDefaults(defineProps<{
  error?: Error | string
  title?: string
  showRetry?: boolean
}>(), {
  title: '出错了',
  showRetry: false
})

const emit = defineEmits<{
  retry: []
}>()

const errorMessage = computed(() => {
  if (!props.error) return '发生了未知错误'
  if (typeof props.error === 'string') return props.error
  return props.error.message || '发生了未知错误'
})

const handleRetry = () => {
  emit('retry')
}
</script>

<style scoped>
