/**
 * 响应式设计 Composable
 * 提供断点检测和响应式工具
 */

import { ref, computed, onMounted, onUnmounted, type ComputedRef, type Ref } from 'vue'

export type Breakpoint = 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl'

export interface BreakpointValues {
  xs: number
  sm: number
  md: number
  lg: number
  xl: number
  '2xl': number
}

export const breakpoints: BreakpointValues = {
  xs: 480,
  sm: 640,
  md: 768,
  lg: 1024,
  xl: 1280,
  '2xl': 1536
}

export interface UseResponsiveReturn {
  // 状态
  windowWidth: Ref<number>
  currentBreakpoint: ComputedRef<Breakpoint>

  // 计算属性
  isMobile: ComputedRef<boolean>
  isTablet: ComputedRef<boolean>
  isDesktop: ComputedRef<boolean>

  // 方法
  isSmaller: (breakpoint: Breakpoint) => ComputedRef<boolean>
  isLarger: (breakpoint: Breakpoint) => ComputedRef<boolean>
  isBetween: (min: Breakpoint, max: Breakpoint) => ComputedRef<boolean>
  responsive: <T>(values: Partial<Record<Breakpoint, T>>, defaultValue: T) => T
  getResponsiveColumns: (config?: {
    xs?: number
    sm?: number
    md?: number
    lg?: number
    xl?: number
    '2xl'?: number
  }) => number
  getResponsiveSpacing: (config?: {
    xs?: number
    sm?: number
    md?: number
    lg?: number
  }) => number

  // 断点值
  breakpoints: BreakpointValues
}

export function useResponsive(): UseResponsiveReturn {
  const windowWidth = ref(typeof window !== 'undefined' ? window.innerWidth : 1024)

  const updateWidth = () => {
    windowWidth.value = window.innerWidth
  }

  onMounted(() => {
    window.addEventListener('resize', updateWidth)
  })

  onUnmounted(() => {
    window.removeEventListener('resize', updateWidth)
  })

  // 当前断点
  const currentBreakpoint = computed<Breakpoint>(() => {
    const width = windowWidth.value
    
    if (width < breakpoints.xs) return 'xs'
    if (width < breakpoints.sm) return 'xs'
    if (width < breakpoints.md) return 'sm'
    if (width < breakpoints.lg) return 'md'
    if (width < breakpoints.xl) return 'lg'
    if (width < breakpoints['2xl']) return 'xl'
    return '2xl'
  })

  // 是否为移动设备
  const isMobile = computed(() => windowWidth.value < breakpoints.md)

  // 是否为平板设备
  const isTablet = computed(() => 
    windowWidth.value >= breakpoints.md && windowWidth.value < breakpoints.lg
  )

  // 是否为桌面设备
  const isDesktop = computed(() => windowWidth.value >= breakpoints.lg)

  // 是否小于指定断点
  const isSmaller = (breakpoint: Breakpoint) => {
    return computed(() => windowWidth.value < breakpoints[breakpoint])
  }

  // 是否大于指定断点
  const isLarger = (breakpoint: Breakpoint) => {
    return computed(() => windowWidth.value >= breakpoints[breakpoint])
  }

  // 是否在指定断点范围内
  const isBetween = (min: Breakpoint, max: Breakpoint) => {
    return computed(() => 
      windowWidth.value >= breakpoints[min] && windowWidth.value < breakpoints[max]
    )
  }

  // 根据断点返回不同的值
  const responsive = <T>(values: Partial<Record<Breakpoint, T>>, defaultValue: T): T => {
    const bp = currentBreakpoint.value
    
    // 从当前断点向下查找
    const breakpointOrder: Breakpoint[] = ['2xl', 'xl', 'lg', 'md', 'sm', 'xs']
    const currentIndex = breakpointOrder.indexOf(bp)
    
    for (let i = currentIndex; i < breakpointOrder.length; i++) {
      const key = breakpointOrder[i]
      if (values[key] !== undefined) {
        return values[key] as T
      }
    }
    
    return defaultValue
  }

  // 获取响应式列数
  const getResponsiveColumns = (config?: {
    xs?: number
    sm?: number
    md?: number
    lg?: number
    xl?: number
    '2xl'?: number
  }): number => {
    const defaultConfig = {
      xs: 1,
      sm: 1,
      md: 2,
      lg: 3,
      xl: 4,
      '2xl': 4
    }
    
    const finalConfig = { ...defaultConfig, ...config }
    return responsive(finalConfig, 1)
  }

  // 获取响应式间距
  const getResponsiveSpacing = (config?: {
    xs?: number
    sm?: number
    md?: number
    lg?: number
  }): number => {
    const defaultConfig = {
      xs: 8,
      sm: 12,
      md: 16,
      lg: 24
    }
    
    const finalConfig = { ...defaultConfig, ...config }
    return responsive(finalConfig, 16)
  }

  return {
    // 状态
    windowWidth,
    currentBreakpoint,
    
    // 计算属性
    isMobile,
    isTablet,
    isDesktop,
    
    // 方法
    isSmaller,
    isLarger,
    isBetween,
    responsive,
    getResponsiveColumns,
    getResponsiveSpacing,
    
    // 断点值
    breakpoints
  }
}
